import React, { useState, useEffect, useCallback } from 'react';
import Modal from '../Common/Modal';
import { getPetById, getPetMedicalHistory, getPetVaccinationHistory } from '../../services/api';
import { Heart, Calendar, Stethoscope, Syringe } from 'lucide-react';

const PetDetailModal = ({ petId, onClose }) => {
  const [pet, setPet] = useState(null);
  const [medicalHistory, setMedicalHistory] = useState([]);
  const [vaccinationHistory, setVaccinationHistory] = useState([]);
  const [loading, setLoading] = useState(true);
  const [activeTab, setActiveTab] = useState('info');

  const loadPetDetail = useCallback(async () => {
    try {
      setLoading(true);
      const [petResponse, medicalResponse, vaccinationResponse] = await Promise.all([
        getPetById(petId),
        getPetMedicalHistory(petId),
        getPetVaccinationHistory(petId)
      ]);
      
      setPet(petResponse.data);
      setMedicalHistory(medicalResponse.data || []);
      setVaccinationHistory(vaccinationResponse.data || []);
    } catch (error) {
      console.error('Error loading pet detail:', error);
    } finally {
      setLoading(false);
    }
  }, [petId]);

  useEffect(() => {
    loadPetDetail();
  }, [loadPetDetail]);

  if (loading) {
    return (
      <Modal isOpen={true} onClose={onClose} title="Chi Tit Th Cng" size="lg">
        <div className="text-center py-12">
          <div className="inline-block w-12 h-12 border-4 border-purple-200 border-t-purple-600 rounded-full animate-spin"></div>
          <p className="mt-4 text-gray-600">ang ti...</p>
        </div>
      </Modal>
    );
  }

  if (!pet) return null;

  return (
    <Modal isOpen={true} onClose={onClose} title="Chi Tit Th Cng" size="lg">
      <div className="space-y-6">
        {/* Thng tin c bn */}
        <div className="bg-gradient-to-r from-purple-50 to-pink-50 rounded-xl p-6">
          <div className="flex items-start gap-6">
            <div className="w-24 h-24 bg-gradient-to-br from-purple-500 to-pink-500 rounded-full flex items-center justify-center text-white text-4xl shadow-lg">
              
            </div>
            <div className="flex-1">
              <h3 className="text-2xl font-bold text-gray-800 mb-2">{pet.TenThuCung}</h3>
              <div className="grid grid-cols-2 gap-3 text-sm">
                <div className="flex items-center gap-2 text-gray-600">
                  <Heart size={16} className="text-pink-500" />
                  <span><strong>Loi:</strong> {pet.LoaiThuCung}</span>
                </div>
                <div className="flex items-center gap-2 text-gray-600">
                  <span><strong>Ging:</strong> {pet.GiongThuCung}</span>
                </div>
                <div className="flex items-center gap-2 text-gray-600">
                  <Calendar size={16} className="text-blue-500" />
                  <span><strong>Ngy sinh:</strong> {pet.NgaySinh ? new Date(pet.NgaySinh).toLocaleDateString('vi-VN') : 'N/A'}</span>
                </div>
                <div className="flex items-center gap-2 text-gray-600">
                  <span><strong>Gii tnh:</strong> {pet.GioiTinhThuCung}</span>
                </div>
              </div>
              <div className="mt-3 p-3 bg-white rounded-lg">
                <p className="text-gray-700"><strong>Ch S Hu:</strong> {pet.TenChuSoHuu}</p>
                <p className="text-gray-700"><strong>ST:</strong> {pet.SDT}</p>
              </div>
            </div>
          </div>
        </div>
        {/* Tabs */}
    <div className="border-b border-gray-200">
      <div className="flex gap-4">
        <button
          onClick={() => setActiveTab('info')}
          className={`px-4 py-2 font-medium border-b-2 transition-colors ${
            activeTab === 'info'
              ? 'border-purple-500 text-purple-600'
              : 'border-transparent text-gray-500 hover:text-gray-700'
          }`}
        >
          Thng Tin
        </button>
        <button
          onClick={() => setActiveTab('medical')}
          className={`px-4 py-2 font-medium border-b-2 transition-colors flex items-center gap-2 ${
            activeTab === 'medical'
              ? 'border-purple-500 text-purple-600'
              : 'border-transparent text-gray-500 hover:text-gray-700'
          }`}
        >
          <Stethoscope size={16} />
          Lch S Khm ({medicalHistory.length})
        </button>
        <button
          onClick={() => setActiveTab('vaccination')}
          className={`px-4 py-2 font-medium border-b-2 transition-colors flex items-center gap-2 ${
            activeTab === 'vaccination'
              ? 'border-purple-500 text-purple-600'
              : 'border-transparent text-gray-500 hover:text-gray-700'
          }`}
        >
          <Syringe size={16} />
          Lch S Tim ({vaccinationHistory.length})
        </button>
      </div>
    </div>

    {/* Tab Content */}
    {activeTab === 'info' && (
      <div className="space-y-4">
        <div className="bg-white border border-gray-200 rounded-xl p-4">
          <h4 className="font-bold text-gray-800 mb-2">Tnh Trng Sc Khe</h4>
          <p className="text-gray-600">{pet.TinhTrangSucKhoe}</p>
        </div>
      </div>
    )}

    {activeTab === 'medical' && (
      <div className="space-y-3">
        {medicalHistory.length === 0 ? (
          <p className="text-gray-500 text-center py-6">Cha c lch s khm bnh</p>
        ) : (
          medicalHistory.map((record, idx) => (
            <div key={idx} className="bg-white border border-gray-200 rounded-xl p-4">
              <div className="flex items-start justify-between mb-2">
                <div>
                  <p className="font-semibold text-gray-800">
                    {record.NgayKham ? new Date(record.NgayKham).toLocaleDateString('vi-VN') : 'N/A'}
                  </p>
                  <p className="text-sm text-gray-600">Bc s: {record.TenBacSi}</p>
                  <p className="text-sm text-gray-600">Chi nhnh: {record.TenChiNhanh}</p>
                </div>
              </div>
              <div className="space-y-2 text-sm">
                <div>
                  <strong className="text-gray-700">Triu chng:</strong>
                  <p className="text-gray-600">{record.TrieuChung}</p>
                </div>
                <div>
                  <strong className="text-gray-700">Chun on:</strong>
                  <p className="text-gray-600">{record.ChuanDoan}</p>
                </div>
                <div>
                  <strong className="text-gray-700">Toa thuc:</strong>
                  <p className="text-gray-600">{record.ToaThuoc}</p>
                </div>
                {record.NgayHenTaiKham && (
                  <div>
                    <strong className="text-gray-700">Ngy hn ti khm:</strong>
                    <p className="text-gray-600">{new Date(record.NgayHenTaiKham).toLocaleDateString('vi-VN')}</p>
                  </div>
                )}
              </div>
            </div>
          ))
        )}
      </div>
    )}

    {activeTab === 'vaccination' && (
      <div className="space-y-3">
        {vaccinationHistory.length === 0 ? (
          <p className="text-gray-500 text-center py-6">Cha c lch s tim chng</p>
        ) : (
          vaccinationHistory.map((record, idx) => (
            <div key={idx} className="bg-white border border-gray-200 rounded-xl p-4">
              <div className="flex items-start justify-between mb-2">
                <div>
                  <p className="font-semibold text-gray-800">
                    {record.NgayTiem ? new Date(record.NgayTiem).toLocaleDateString('vi-VN') : 'N/A'}
                  </p>
                  <p className="text-sm text-gray-600">Bc s: {record.TenBacSi}</p>
                  <p className="text-sm text-gray-600">Chi nhnh: {record.TenChiNhanh}</p>
                </div>
              </div>
              <div className="space-y-2 text-sm">
                <div>
                  <strong className="text-gray-700">Vc-xin:</strong>
                  <p className="text-gray-600">{record.TenVacXin} ({record.LoaiVacXin})</p>
                </div>
                <div>
                  <strong className="text-gray-700">Mi tim:</strong>
                  <p className="text-gray-600">{record.TenMuiTiem}</p>
                </div>
                <div>
                  <strong className="text-gray-700">Liu lng:</strong>
                  <p className="text-gray-600">{record.LieuLuong}</p>
                </div>
              </div>
            </div>
          ))
        )}
      </div>
    )}
  </div>
</Modal>
    );
};

export default PetDetailModal;
